import { Component, OnInit, ViewChild } from '@angular/core';
import { PresupuestoService } from '../../service/presupuesto.service';
import { DataTableDirective } from 'angular-datatables';
import { Presupuesto } from '../../models/presupuesto';
import { Subject } from 'rxjs';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-lista',
  templateUrl: './lista.component.html',
  styleUrls: ['./lista.component.scss']
})
export class ListaComponent implements OnInit {
  breadCrumbItems: Array<{}>;
  presupuestos : Presupuesto []
  presupuesto !: Presupuesto
  showLoading = false;

  @ViewChild(DataTableDirective, { static:false}) dtElement: DataTableDirective
  dtOptions: any = {};
  dtOptionsD: any = {};
  dtTrigger: Subject<Presupuesto> = new Subject<Presupuesto>();
  constructor(
    private presupuestoService: PresupuestoService,
    private modalService: NgbModal
  ) { }

  ngOnInit(): void {
    this.breadCrumbItems = [{ label: 'Presupuesto' }, { label: 'Lista', active: true }];
    this.dtOptions = {
      columnDefs:[
        {className: "", targets:[0,1,2,3,4]},
        {orderable:false, targets:[4]},
        {searchable: false, targets:[0,4]},
        // {width: "30%", targets:[2]},
        // {width: "10%", targets:[0,1,2,3,4]},
        { responsivePriority: 1, targets: -1 },
      ],
      lengthMenu:[5,10,20,50],
      destroy: true,
      language: {
        url: '//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json',
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningún dato encontrado",
      },
      pagingType: 'full_numbers',
      responsive:true
    };
    this.cargar()
    this.loadTable()
  }
  ngOnDestroy(){
    this.dtTrigger.unsubscribe();
  }
  cargar(){
    this.presupuestoService.presupuestos().subscribe((resp: Presupuesto[]) => {
      this.presupuestos = resp;
      this.dtTrigger.next(null)
      this.presupuesto
    });
  }
  reload(){
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) =>{
      this.presupuestos = []
      dtInstance.destroy()
    })
    this.cargar()
  }
  openModal(agg: any, edt: Presupuesto) {
    this.presupuesto = edt
    this.modalService.open(agg, { size: 'lg'})
  }
  eliminar(x: Presupuesto) {
    Swal.fire({
      title: '¿Está seguro de eliminar? ',
      text: `Esto no se puede revertir`,
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: "#a90000",
      cancelButtonColor: '#343a40',
      confirmButtonText: 'Sí, borrar',
      cancelButtonText:'Cancelar'
    }).then((result) => {
      if (result.isConfirmed) {
        this.presupuestoService.eliminar(x).subscribe(resp => {
          console.log(resp);
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Eliminado',
            text: `El registro ha sido eliminado`,
            icon: 'success',
          });
          this.reload()
        },
          (err: any) => {
            Swal.fire({
              confirmButtonColor: "#a90000",
              icon: 'error',
              title: 'Error',
              text: err.error.msg
            })
          });
      } else if (result.isDenied) {
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: "Información",
          text: `Cambios no aplicados`,
          icon: "info",
        });
      }
    });
  }
  loadTable(){
    this.dtOptionsD = {
      searching: false,
      columnDefs: [
        { orderable: false, targets: 0 },
        // {width: "30%", targets:[3]},
        // {width: "5%", targets:[0,1,2,4]},
        { responsivePriority: 1, targets: -1 }
      ],
      lengthMenu: [5, 10, 20, 50],
      destroy: true,
      language: {
        url: '//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json',
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningún dato encontrado",
      },
      pagingType: 'full_numbers',
      responsive: true
    };
  }

  descargarPdfParametro(pdf: number) {
    this.showLoading = true;
    this.presupuestoService
      .generarPdfPresupuesto(pdf)
      .subscribe((data: Blob) => {
        this.showLoading = false;
        const blob = new Blob([data], { type: "application/pdf" });
        const link = document.createElement("a");
        let url = URL.createObjectURL(blob);
        window.open(url);
      });
  }
}
