import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Presupuesto } from '../models/presupuesto';
import { Observable } from 'rxjs';
import { Presupuestos } from '../models/detalle';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class PresupuestoService {
  url = `${environment.urlG}/registropresupuestos`

  constructor(
    private http: HttpClient
  ) { }
  // public presupuestos(page: number = 0, size: number = 3): Observable<any> {
  //   return this.http.get<any>(`${this.url}/presupuesto` + '?' + `page=${page}&size=${size}`)
  // }
  public presupuestos(): Observable<Presupuesto[]> {
    return this.http.get<Presupuesto[]>(`${this.url}`)
  }
  getDetalle(id: number): Observable<Presupuesto> {
    return this.http.get<Presupuesto>(`${this.url}`)
  }
  eliminar(x: Presupuesto) {
    return this.http.delete(`${this.url}/${x.id}`)
  }
  guardar(x: Presupuestos) {
    return this.http.post(`${this.url}`, x)
  }
  generarPdfPresupuesto(id:number) {
    return this.http.get<Blob>(`${this.url}/pdf` + '?' + `id=${id}`, { responseType: 'arraybuffer' as 'json' });
  }

}
