import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { PresupuestoService } from '../../service/presupuesto.service';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { Detalles, Presupuestos } from '../../models/detalle';
import Swal from 'sweetalert2';
import { NgbDate, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { isCaracter, isDescription } from 'src/app/constantes/constantesproyecto';
import { AutenticacionService } from 'src/app/account/auth/login/service/autenticacion.service';
import { UsuarioA } from 'src/app/account/auth/login/models/usuario';

@Component({
  selector: 'app-registro',
  templateUrl: './registro.component.html',
  styleUrls: ['./registro.component.scss']
})
export class RegistroComponent implements OnInit {
  breadCrumbItems: Array<{}>;
  formulario !: FormGroup;
  formT !: FormGroup;
  presupuesto: Presupuestos;
  presupuestos: Presupuestos[];
  fecha: string;
  detalle !: Detalles;
  detalles: Detalles[] = [];
  totalF: number;
  habilitar: boolean = true
  user !: UsuarioA
  showLoading = false;

  @ViewChild(DataTableDirective, { static: false }) dtElement: DataTableDirective;
  dtOptions: any = {};
  dtTrigger: Subject<Detalles> = new Subject();
  constructor(
    private presupuestoService: PresupuestoService,
    private fb: FormBuilder,
    private authService: AutenticacionService,
    private modalService: NgbModal
  ) {
    this.formulario = this.inif()
    this.formT = this.inifT()
  }

  ngOnInit(): void {
    this.breadCrumbItems = [{ label: 'Presupuesto' }, { label: 'Registro', active: true }];
    this.user = this.authService.getUserFromLocalCache();
    this.dtOptions = {
      searching: false,
      columnDefs: [
        { orderable: false, targets: [0, 4] },
        { searchable: false, targets: [0, 4] },
        // { width: "15%", targets: [0, 2, 3, 4] },
        // { width: "40%", targets: [1] },
        { responsivePriority: 1, targets: -1 },
      ],
      lengthMenu: [5, 10, 20, 50],
      destroy: true,
      language: {
        url: '//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json',
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningún dato encontrado",
      },
      pagingType: 'full_numbers',
      responsive: true,
    };
    this.getDetalles()
  }

  private inif(): FormGroup {
    return this.fb.group({
      descripcion: ['', [Validators.required, Validators.minLength(4)]],
      cantidad: ['', [Validators.required, Validators.min(1), Validators.max(200)]],
      precio: ['', [Validators.required, Validators.min(0.01), Validators.max(5000)]],
    })
  }
  private inifT(): FormGroup {
    return this.fb.group({
      titulo: ['', [Validators.required, Validators.minLength(4), Validators.pattern(isCaracter)]],
    })
  }
  esCampoValido(campo: string) {
    const validarCampo = this.formulario.get(campo)
    return !validarCampo?.valid && validarCampo?.touched
      ? 'is-invalid' : validarCampo?.touched ? 'is-valid' : '';
  }
  esCampoValidoT(campo: string) {
    const validarCampo = this.formT.get(campo)
    return !validarCampo?.valid && validarCampo?.touched
      ? 'is-invalid' : validarCampo?.touched ? 'is-valid' : '';
  }
  agregar() {
    // const descripcion = this.formulario.get('descripcion').value;
    // const cantidad = this.formulario.get('cantidad').value;
    // const precio = this.formulario.get('precio').value;
    // this.detalle = new Detalles(descripcion, cantidad, precio);
    // this.detalles.push(this.detalle);
    const descripcion = this.formulario.get('descripcion').value;
    const cantidad = this.formulario.get('cantidad').value;
    const precio = this.formulario.get('precio').value;
    const detalleExistente = this.detalles.find(detalle => detalle.descripcion.toLowerCase() === descripcion.toLowerCase());
    if (detalleExistente) {
      detalleExistente.cantidad += cantidad;
      if (detalleExistente.preciounidad !== precio) {
        const nuevoDetalle = new Detalles(descripcion, cantidad, precio);
        this.detalles.push(nuevoDetalle);
      }
    } else {
      const nuevoDetalle = new Detalles(descripcion, cantidad, precio);
      this.detalles.push(nuevoDetalle);
    }
    this.reload()
    this.formulario.reset();
  }
  guardar() {
    this.showLoading = true
    const titulo = this.formT.get('titulo').value
    this.presupuesto = new Presupuestos(titulo, this.user.id, this.detalles)
    if (this.formT.valid) {
      this.showLoading = false
      Swal.fire({
        title: ' ¿Está seguro de guardar? ',
        text: `Una vez guardado el presupuesto no puede ser editado`,
        icon: 'question',
        showCancelButton: true,
        confirmButtonColor: "#a90000",
        cancelButtonColor: '#343a40',
        confirmButtonText: 'Sí, guardar',
        cancelButtonText: 'Cancelar'
      }).then((result) => {
        if (result.isConfirmed) {
          this.presupuestoService.guardar(this.presupuesto).subscribe(resp => {
            Swal.fire({
              confirmButtonColor: "#a90000",
              title: 'Éxito',
              text: `Almacenado correctamente`,
              icon: 'success',
            });
          })
          this.detalles.length = 0;
          this.formulario.reset();
          this.formT.reset();
          this.reload()
        } else if (result.isDenied) {
          this.showLoading = false
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: "Información",
            text: `Cambios no aplicados`,
            icon: "info",
          });
        }
      });
    }
    else {
      this.showLoading = false
      Swal.fire({
        confirmButtonColor: "#a90000",
        position: 'center',
        title: 'Falta título de presupuesto',
        icon: 'warning'
      })
      return Object.values(this.formT.controls)
        .forEach(control =>
          control.markAsTouched())
    }
  }
  eliminarFila(x: number) {
    this.detalles.splice(x, 1);
    this.reload()
  }
  ngOnDestroy() {
    this.dtTrigger.unsubscribe();
  }
  calcularTotalF(): number {
    this.totalF = 0.00;
    if (this.detalles.length == 0) {
      return parseFloat(this.totalF.toFixed(2));
    }
    else {
      this.detalles.forEach((detalle: Detalles) => {
        this.totalF += detalle.totalx();
      })
      return parseFloat(this.totalF.toFixed(2));
    }
  }
  limpiar() {
    this.formulario.get('descripcion').setValue("");
    this.formulario.get('cantidad').setValue("");
    this.formulario.get('precio').setValue("");
  }
  getDetalles() {
    return this.detalles
  }
  ngAfterViewInit(): void {
    this.dtTrigger.next();
  }
  reload() {
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
      // this.detalles = []
      dtInstance.destroy()
      this.dtTrigger.next()
    })
    this.getDetalles()
  }
  openModalA(content) {
    this.modalService.open(content, { keyboard: false, scrollable: true, size: 'lg' });
  }
}
